.programm {
  position: relative;
  padding: 16rem 0 0;

  @media (max-width: $tablet) {
    padding: 11rem 0 0;
  }

  &__top-circle {
    position: absolute;
    top: 0;
    left: -18%;
    width: 137%;
    height: 196.8rem;
    border-radius: 50%;
    background-color: #fff;
    z-index: 3;
    overflow: hidden;

    &--bottom {
      bottom: 35rem;
      z-index: 2;
      top: auto;
    }

    @media (max-width: $tablet) {
      top: 0;
      left: 50%;
      height: 1024px;
      width: 1024px;
      transform: translateX(-50%);

      &--bottom {
        top: 200px;
        bottom: auto;
      }
    }
  }

  &__trees-bg {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 10%;
    left: 0;
    background-image: url("../img/trees-bg.svg");
    background-repeat: no-repeat;
    background-position: 50% 0;
    z-index: 2;
  }

  &__date {
    display: flex;
    justify-content: center;
    color: $black;
    text-align: center;
    margin: 0 0 20px;
    font-size: 2.6rem;
    line-height: 1;
    font-weight: 700;
    text-transform: uppercase;

    &--mt {
      margin-top: 5rem;
    }

    span {
      padding: 0 0 20px;
      border-bottom: 2px solid $blue;
    }

    @media (max-width: $tablet) {
      font-size: 18px;
    }
  }

  &__snows {
    margin-top: 1.6rem;
    width: 100%;
    height: 18.2rem;
    background-image: url("../img/snows.svg");
    background-position: 50% 0;
    background-repeat: no-repeat;

    @media (max-width: $tablet) {
      height: 88px;
      background-position: 50% 0;
      background-size: 69%;
    }
  }

  &__content {
    position: relative;
    z-index: 4;
    margin: 0 0 9.5rem;
    @media (max-width: $tablet) {
      margin: 0 0 36px;
    }
  }

  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 0 3.6rem;
  }

  &__item {
    color: $black;
    text-align: center;
    font-weight: 500;
    font-size: 3rem;
    line-height: 3.6rem;
    margin: 0 0 3.5rem;

    &:last-child {
      margin: 0;
    }

    p {
      margin: 0 1rem;

      &:last-child {
        margin: 0;
      }
    }

    span {
      font-size: 18px;
      font-style: italic;
      font-weight: 400;
      // color: $blue;
    }

    @media (max-width: $tablet) {
      font-size: 14px;
      line-height: 17px;
      margin: 0 0 15px;
    }
  }

  &__subtitle {
    color: $blue;
  }
}
